export default {
	data() {
		return {
			drop: false,
			sortmen: false,
			synthesize: '综合排序',
			ying: false,
			param: {},
			sortlist: [
				{
					name: '综合排序',
					screen: '_id',
					nums: 1
				},
				{
					name: '起送价最低',
					screen: 'delivering',
					nums: 1
				},
				{
					name: '配送费最低',
					screen: 'physical',
					nums: 1
				},
				{
					name: '人均高到低',
					screen: 'capita',
					nums: -1
				},
				{
					name: '人均低到高',
					screen: 'capita',
					nums: 1
				}
			],
			// 多选
			screendata: [
				{
					title: '商家特色(可多选)',
					datas: [
						{
							id: 1,
							show: false,
							sign: 'duration',
							name: '配送最快'
						},
						{
							id: 2,
							show: false,
							sign: 'deliver',
							name: '0元起送'
						},
						{
							id: 3,
							show: false,
							sign: 'physi',
							name: '免配送费'
						}
					]
				}
			],
			// 人均价筛选
			person: [
				{
					title: '人均价',
					datas: [
						{
							id: 1,
							show: false,
							name: '20元以下',
							per: {
								$lt: 20
							}
						},
						{
							id: 2,
							show: false,
							name: '20-40元',
							per: {
								$lte: 40,
								$gte: 20
							}
						},
						{
							id: 3,
							show: false,
							name: '40元以上',
							per: {
								$gt: 40
							}
						}
					]
				}
			]
		};
	},
	methods: {
		// 综合排序
		multiple() {
			this.drop = true;
			this.sortmen = false;
			this.backOne();
		},
		// 选择筛选框
		sortClick({ name, screen, nums }) {
			this.synthesize = name;
			this.backClic();
			this.$emit('poststartingurl', { screen, nums });
		},
		// 完成
		complete(){
			if(JSON.stringify(this.param)==='{}') return
			this.$emit('postmultipleurl', this.param);
			this.backClic();
		},
		// 隐藏透明背景
		backClic() {
			this.ying = false;
			this.drop = false;
			this.sortmen = false;
		},
		// 筛选
		scReen() {
			this.drop = false;
			this.sortmen = true;
			this.backOne();
		},
		// 透明背景
		backOne() {
			if (!this.ying) {
				setTimeout(() => {
					this.ying = true;
				}, 310);
			}
		},
		// 点击销售量 速度 津贴
		saLes() {
			this.backClic();
		},
		// 多选
		multipleChoice(item) {
			const find = this.screendata[0].datas.find(v => v.id === item.id);
			find.show = !find.show;
			if (find.show) {
				this.$set(this.param, find.sign, find.sign);
			} else {
				this.$delete(this.param, find.sign);
			}
			console.log(this.param)
		},
		// 单选
		singleChoice(item) {
			this.person[0].datas.forEach(v => {
				if (v.id === item.id) {
					v.show = !v.show;
					if (v.show) {
						this.$set(this.param, 'capita', v.per);
					} else {
						this.$delete(this.param, 'capita');
					}
				} else {
					v.show = false;
				}
			});
			console.log(this.param)
		},
		// 清空
		empty() {
			this.modifyShow(this.person[0].datas);
			this.modifyShow(this.screendata[0].datas);
			this.param={}
			this.backClic();
		},
		modifyShow(arr) {
			arr.forEach(v => {
				v.show = false;
			});
		}
	}
};
